  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 407px;
  position: relative;
}

.card__product {
  color: #060b35;
  padding-inline: 16px;
  padding-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
  display: block;
}

.card__code {
  color: #616070;
  padding-inline: 16px;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  display: flex;
}

.card__button {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-inline: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

