}

.cash__name--price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.cash__sum--value-of-price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.rating {
  padding-inline: 16px;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 24px;
